import { playsound } from './sound.js'
import { drawImage, overlaps } from './util.js'

class Van {
    constructor(x, y, config) {
        this.x = x
        this.y = y
        this.c = config

        this.ismoving = true
        this.hasbeeped = false

        this.rot = 0
        this.bob = 0
        this.inc = 1
        this.count = 0

        this.hbx = this.x + this.c[19].width - 30
        this.hby = this.y
        this.hbw = 30
        this.hbh = this.c[13].height
    }

    render(c) {
        // draw the van
        c.drawImage(this.c[19], this.x, this.y - this.bob)
        // and the wheels
        // front
        drawImage(c, this.c[20], this.x + 131, this.y + 78, 44, 44, this.rot)
        // back
        drawImage(c, this.c[20], this.x + 23, this.y + 78, 44, 44, this.rot)

        // render the hitbox
        /*c.strokeStyle = 'red'
        c.beginPath()
        c.rect(this.hbx, this.hby, this.hbw, this.hbh)
        c.stroke()*/
    }

    update() {
        // bob up and down
        
        if(this.count > 3) {
            this.count = 0
            this.bob += this.inc
            if(this.bob > 1) {
                this.inc = -1
            }
            if(this.bob < 0) {
                this.inc = 1
            }
        }
        this.count++

        // wheels rotate if we're moving
        if(this.ismoving) {
            this.rot -= 2
            if(this.rot < -360) {
                this.rot = 0
            }
        }
    }

    collideWith(list) {
        for(let i = 0; i < list.length; i++) {
            if(!list[i].grabbed) {
                if(list[i].name) {
                    //console.log('Checking ' + list[i].name)
                    
                    //console.log(this.hbx, this.hby, this.hbw, this.hbh)
                    //console.log(list[i].hbx, list[i].hby, list[i].hbw, list[i].hbh)

                    let r = overlaps(this.hbx, this.hby, this.hbw, this.hbh,
                        list[i].hbx, list[i].hby, list[i].hbw, list[i].hbh)
                    //console.log('r is ', r)
                }

                if(overlaps(this.hbx, this.hby, this.hbw, this.hbh,
                            list[i].hbx, list[i].hby, list[i].hbw, list[i].hbh
                ) > 0.0) {
                    this.beep()
                    this.ismoving = false
                    return true
                }
            }
        }
        this.hasbeeped = false
        this.ismoving = true
        return false
    }

    beep() {
        if(!this.hasbeeped) {
            playsound(1)
            window.setTimeout(playsound, 400, 1)
            this.hasbeeped = true
        }
    }
}

export default Van